import {Button, Col, Modal, Row} from "reactstrap";
import React, {useState} from "react";
import {offset} from "ol/sphere";
import CustomModals from "../../views/ui/CustomModal";


function UserDetailInformation(props) {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [pwData, setPwData] = useState({
        pw: "",
        repw: ""
    })

    function userInputDiv() {
        return (
            <Row>
                <Col xl={12}>
                    <input className={"pwChangeInput"}
                           placeholder={"변경할 비밀번호를 입력해주세요."}
                           onChange={event => setPwData({...pwData, pw: event.target.value})}
                    />
                </Col>
                <Col xl={12}>
                    <input className={"pwChangeInput"}
                           placeholder={"변경할 비밀번호를 한번 더 입력해주세요."}
                           onChange={event => setPwData({...pwData, repw: event.target.value})}
                    />
                </Col>
            </Row>
        )
    }

    return <Row className={'userRow'}>
        <Col className={'user-col'}>
            <h4 className={'text-white mgl'}>상세 정보</h4>
        </Col>
        <Row className={'detailPaddingRow'}>
            <Row className={'firstRow'}>
                <Col xs={3} className={'text-white-50'}>
                    ID
                </Col>
                <Col xs={3} className={"col-box height24"}>
                    <p className={"col-p-content text-white-50"}>{props.userDetailData.memberId}</p>
                </Col>
                <Col xs={3} className={'text-white-50'}>
                    PW
                </Col>
                <Col xs={3} className={"col-box height24 pw"} onClick={handleShow}>
                    <p className={"text-white-50"}>변경을 원하시면 클릭해주세요.</p>
                </Col>
            </Row>

            <Row>
                <Col xs={3} className={'text-white'}>
                    이름
                </Col>
                <Col xs={3} className={'col-box mgl'}>
                    <input
                        onChange={props.handleChange}
                        name="name"
                        value={props.userDetailData.name || ""}
                        className={'userDetailInput'}
                        placeholder={props.userDetailData.name}
                    />
                </Col>
                <Col xs={3} className={"text-white-50 mgl"}>
                    권한
                </Col>
                <Col xs={3} className={"p-grey"}>
                    <input
                        onChange={props.handleChange}
                        name="role"
                        value={props.userDetailData.role || ""}
                        className={'userDetailInput'}
                        placeholder={props.userDetailData.role}
                    />
                </Col>
            </Row>


            <Row>
                <Col xs={3} className={'text-white-50 '}>
                    가입날짜
                </Col>
                <Col xs={3} className={"col-box p-grey height24"}>
                    <p className={"col-p-content"}>{props.userDetailData.registerDate}</p>
                </Col>
                <Col xs={3} className={'text-white'}>
                    비고
                </Col>
                <Col xs={3} className={"height24"}>
                    <input onChange={props.handleChange}
                           name={"userNote"}
                           value={props.userDetailData.userNote || " "}
                           className={'userDetailInput'}
                           placeholder={props.userDetailData.userNote}>
                    </input>
                </Col>
            </Row>
        </Row>

        <Row className={'button-row'}>

            <Col xs={2} md={{offset: 5}} className={"col-button"}>
                <Button
                    color="primary"
                    size="sm"
                    className={'btn blue'}
                >
                    수정
                </Button>
            </Col>
            <Col className={"col-button"}>
                <Button
                    color="primary"
                    size="sm"
                    className={'btn green'}
                >
                    신규
                </Button>
            </Col>
            <Col className={"col-button"}>
                <Button
                    color="primary"
                    size="sm"
                    className={'btn red'}
                >
                    삭제
                </Button>
            </Col>
        </Row>
        <CustomModals title={"PW 변경"} onClick={handleShow} children={userInputDiv()} handleClose={handleClose}
                      show={show}/>
    </Row>
}

export default UserDetailInformation