import React, {useState} from "react";
import {Col, FormGroup, Input, Row, Table} from "reactstrap";

function UserSearchTable(props) {

    const [selectedItem, setSelectedItem] = useState(null);


    const handleCheckboxChange = (item) => {
        const newData = {
            name: item.name,
            memberId: item.memberId,
            role: item.role,
            registerDate: item.registerDate,
        };
        props.setUserDetailData(newData);

        if (selectedItem && selectedItem.systemCode === item.systemCode) {
            setSelectedItem(null);
        } else {
            setSelectedItem(item);
        }
    };

    return (
        <Row className={"userRow"}>
            <Col xl={12}>
                <h4 className={'text-white mgl'}>정보 조회</h4>
            </Col>
            <Col>
                <Table className={props.customClassName} hover size="">
                    <thead>
                    <tr className={"table-head"}>
                        {props.tableHeadData.map((item, index) => (
                            <th key={index}>{item}</th>
                        ))}
                    </tr>
                    </thead>
                    <tbody>
                    {props.userSearchData.map((item, index) => (
                        <tr key={index}>
                            <td>
                                <FormGroup check inline>
                                    <Input
                                        type="checkbox"
                                        onChange={() => handleCheckboxChange(item)}
                                        checked={selectedItem && selectedItem.memberId === item.memberId}
                                    />
                                </FormGroup>
                            </td>
                            <td>{item.name}</td>
                            <td>{item.memberId}</td>
                            <td>{item.role}</td>
                            <td>{item.registerDate}</td>
                        </tr>
                    ))}
                    </tbody>
                </Table>
            </Col>
        </Row>
    );
}

export default UserSearchTable