import React, {useEffect, useState} from 'react';
import {Button} from 'reactstrap';
import Modal from 'react-bootstrap/Modal';

function CustomModals(props) {

    return (
        <div
            className="modal show"
            style={{ display: 'block', position: 'initial' }}
        >
            <Modal size={props.modalSize !== null ? props.modalSize : " "} show={props.show} onHide={props.onHide}>
            <Modal.Dialog >

                <Modal.Header>
                    <Modal.Title>{props.title}</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    {props.children}
                </Modal.Body>

                <Modal.Footer>
                    <Button className={'btn red'} onClick={props.handleClose}>취소</Button>
                    <Button className={'btn blue'}>변경</Button>
                </Modal.Footer>
            </Modal.Dialog>
            </Modal>
        </div>
    );
}

export default CustomModals;