import {Button, Col, Row} from "reactstrap";
import React from "react";
import * as Dimensions from "ol/proj";

function UserSearch() {

    return (
        <div>
            <Row className={'userRow'}>
                <Col>
                    <h4 className={'text-white mgl'}>정보 조회</h4>
                </Col>
                <Col>
                    <Button className="btn blue float-sm-end" >
                        조회
                    </Button>
                </Col>
                <Row className={'paddingRow'}>
                    <Col>
                        <p className={'text-white weight'}>사용자명</p>
                    </Col>
                    <Col>
                        <p className={'text-white weight'}>아이디명</p>
                    </Col>
                    <Col>
                        <p className={'text-white weight'}>권한</p>
                    </Col>
                </Row>
                <Row className={'paddingRow mgt'}>
                    <Col>
                        <input className={'userSearchInput'}/>
                    </Col>
                    <Col>
                        <input className={'userSearchInput'}/>
                    </Col>
                    <Col>
                        <input className={'userSearchInput'}/>
                    </Col>
                </Row>
            </Row>
        </div>
    )
}

export default UserSearch