import FullLayout from "../layouts/FullLayout";
import Header from "../layouts/Header";
import "../assets/scss/layout/user.scss"
import {Col, Row} from "reactstrap";
import UserSearch from "../components/user/userSearch";
import UserSearchTable from "../components/user/userSearchTable";
import React, {useEffect, useState} from "react";
import UserDetailInformation from "../components/user/UserDetailInformation";

function User(){
    const headerData = "사용자 관리"
    const tableHeadData = [" " , "사용자명", "아이디명" , "권한", "등록일","비고"]
    const userSearchData = [
        {
            memberId: "aa2",
            name: "nextGreen",
            role: "admin",
            registerDate: "2023-02-01"
        },
        {
            memberId: "aa22",
            name: "nextGreen",
            role: "admin",
            registerDate: "2023-02-01"
        },
        {
            memberId: "aa23",
            name: "nextGreen",
            role: "admin",
            registerDate: "2023-02-01"
        },
        {
            memberId: "aa42",
            name: "nextGreen",
            role: "admin",
            registerDate: "2023-02-01"
        }

    ]

    const [userDetailData, setUserDetailData] = useState({
        name: "",
        memberId: "",
        role: "",
        registerDate: "",
        userNote: ""
    });
    const handleChange = (e) => {
        const {name, value} = e.target;
        setUserDetailData({
            ...userDetailData,
            [name]: value,
        });
    };
    useEffect(()=>{
        console.log(userDetailData)
    },[userDetailData])
    return(
        <div>
            <div id="card-wrap">
                <Header title={headerData} navClassName={"user"}/>
                <UserSearch/>
                <UserSearchTable customClassName={'userSearchTable'} userSearchData={userSearchData} setUserDetailData={setUserDetailData} tableHeadData={tableHeadData}/>
                <UserDetailInformation handleChange={handleChange} userDetailData={userDetailData}/>
            </div>
        </div>
    )
}
export default User